

























































import { Component, Vue, Prop, Emit } from 'vue-property-decorator';
import GameMenu from '@/components/GameIframe/GamePopup/GameMenu.vue';

@Component({
  components: {
    GameMenu
  }
})

export default class GameNav extends Vue {
  private value?: boolean = false;
  private isShowDropMenu?: boolean = false;
  private isGameMenu?: boolean = false;
  private hasMenu?: boolean = false;
  private gameTypeList?: any = [
    'PK10',
    '',
    '',
    '',
    '115'
  ];
  private gameList?: object | any = {
    'PK10': [
      { name: '', isHot: false },
      { name: '', isHot: false },
      { name: '', isHot: false },
      { name: '', isHot: false },
      { name: '', isHot: true },
      { name: '', isHot: false },
      { name: '', isHot: false }
    ],
    '': [
      { name: '', isHot: true },
      { name: '', isHot: false },
      { name: '', isHot: false }
    ],
    '': [
      { name: '', isHot: false },
      { name: '', isHot: false },
      { name: '', isHot: false },
      { name: '', isHot: false },
      { name: '', isHot: false },
      { name: '', isHot: false }
    ],
    '': [
      { name: '', isHot: false },
      { name: '', isHot: false },
      { name: '', isHot: false },
      { name: '', isHot: false },
      { name: '', isHot: false },
      { name: '', isHot: false },
      { name: '', isHot: false },
      { name: '', isHot: false },
      { name: '', isHot: false },
      { name: '', isHot: false },
      { name: '', isHot: false }
    ],
    '115': [
      { name: '115', isHot: false },
      { name: '115', isHot: false },
      { name: '115', isHot: false },
      { name: '115', isHot: false }
    ],
  };
  @Emit('openDropMenu') sendOpen(value: any) {};
  private openDropMenu() {
    this.onCloseMenu(false);
    this.sendOpen(true);
  }
  @Emit('closeDropMenu') sendClose(value: any) {};
  private closeDropMenu() {
    this.sendClose(false);
  }
  private goBackPage() {
    this.$router.back();
  }
  private hideDropDown() {
    let _item = this.$refs.item as any;
    _item.toggle();
  }
  private selectItem(val: any) {
    this.$store.dispatch('setPlayGroupName', val)
    this.hideDropDown();
  }
  @Emit('showMenu') sendMenu(value: any) {};
  private showMenu() {
    this.isGameMenu = !this.isGameMenu;
    this.sendMenu(this.isGameMenu);
  }
  @Emit('onOpenMenu') sendOpenMenu(value: any) {};
  private onOpenMenu(val: any) {
    this.hasMenu = val;
    this.sendOpenMenu(this.hasMenu)
  }
  @Emit('onCloseMenu') sendColseMenu(value: any) {};
  private onCloseMenu(val: any) {
    this.hasMenu = val;
    this.isGameMenu = false;
    this.sendColseMenu(this.hasMenu)
  }
  get playGroupName() {
    return this.$store.state.gameBet.playGroup.name;
  }
  private mounted() {
    if (this.playGroupName == '') {
      this.$store.dispatch('setPlayGroupName', this.gameList['PK10'][0].name)
    }
  }
}
